.search{
    width: 81%;
    display: flex;
    justify-content: center;
    position: absolute;
    top: 10rem;
  }
  
  .search Input {
    width: 48%;
    height: 2.5rem;
    border: 2px solid black;
    background: #e8e3cd;
  }
  
  .search Button {
    background-color: #3e5962;
    border: 2px solid #000;
    width: 13%;
    height: 2.5rem;
  
    &:hover{
      background-color: #192d33;
    }
  }